<template lang="html">
  <div>
    <slot name="title"></slot>
    <div class="lists clearFix">
      <slot name="laest" class="laest"></slot>
      <router-link :to="{ name: 'video', params: {id:video.id} }" v-for="video in videos.laest" :key="video.id" class="videoLink">
        <img :src="video.img_url" alt="">
      </router-link>
    </div>
    <div class="lists clearFix">
      <slot name="hot"></slot>
      <router-link :to="{ name: 'VideoWrapper', params: {id:video.id} }" v-for="video in videos.hot" class="videoLink" :key="video.id">
        <img :src="video.img_url" alt="">
      </router-link>
    </div>

  </div>

</template>

<script>
export default {
  props:['videos']
}
</script>

<style lang="scss" scoped>
  .lists{
    margin-bottom: 26px;
    .laest,.hot{
      float: left;
      margin-right: 26px;
      margin-bottom: 26px;
    }
    img{
      width: 248px;
      height: 142px;
    }
    .videoLink{
      display: block;
      float: left;
      margin-right: 26px;
      margin-bottom: 26px;
    }
  }

</style>



