<template lang="html">
  <div>
    <group-videos v-for="videoList in areaVideos" :videos="videoList.lists" :key="videoList.id">
      <h2 class="title" slot="title">{{ videoList.title }}</h2>
      <div class="laest" slot="laest">
        <div class="text">{{ laest }}</div>
      </div>
      <div class="hot" slot="hot">
        <div class="text">{{ hot }}</div>
      </div>
    </group-videos>
  </div>

</template>

<script>
import groupVideos from '../components/GroupVideos.vue'
import faker from '../../static/faker'
export default {
    data () {
      return{
        areaVideos : [],
        laest : '最新更新',
        hot : '一周热门'
      }

    },
    components:{
      groupVideos
    },
    created () {
      this.areaVideos = faker.groupVideosData();
    }
}
</script>

<style lang="scss" scoped>
  .title{
    text-align: left;
  }
  .laest,.hot{
    background:#1fb5ad;
    display: flex;
    width: 248px;
    height: 142px;
    >.text{
      margin: auto;
      color:#fff;
      font-size: 26px;
    }
  }
</style>



