<template lang="html">
  <div class="comments_container">
    <div class="lists clearFix" v-for="list in lists">
      <div class="author">
        <img :src="list.faceImg" alt="">
      </div>
      <div class="info">
        <a>{{ list.userName }}</a>
        <div class="text">{{ list.content }}</div>
      </div>

    </div>
    <pages></pages>
  </div>
</template>

<script>
import Pages from './Pages.vue'
import { mapGetters } from 'vuex'
export default {
  components:{Pages},
  computed:mapGetters({lists:'comments'})
}
</script>

<style lang="scss" scoped>
  .comments_container{
    margin-top: 20px;
    .lists{
      margin-bottom: 26px;
      color:#fff;
      .author{
        float: left;
        margin-top: 16px;
        img{
          width: 48px;
          height: 48px;
          border-radius: 50%;
        }
      }
      .info{
        margin-left: 80px;
        background: #1fb5ad;
        padding:16px;
        min-height: 60px;
        .text{
          margin-top: 7px;
        }
      }
  }
}
.page{
  float: right;
  color:#fff;
  a,span{
    margin-left: 5px;
  }
}
</style>



