    }
  }
</script>
<style  scoped lang="scss">
  $bgColor : #1fb5ad;
  .list{
    color: #fff;
    margin-bottom: 42px;
    display: block;
    >.l-type{
      float: left;
      background:  $bgColor;
      width: 18%;
      line-height: 88px;
      box-sizing: border-box;
      text-align: center;
      vertical-align: middle;
    }
    >.r-text{
      float: right;
      width: 75%;
      background:  $bgColor;
      height: 88px;
      line-height: 88px;
      box-sizing: border-box;
      padding: 0 10px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space:nowrap;
    }
}

</style>



