<template lang="html">
  <div class="wrapper clearFix">
    <div class="top clearFix">
      <h2>{{ title }}</h2>
      <!-- <div class="author clearFix">
        <a>
          <img :src="author.img_url" alt="">
        </a>
        <div class="info">
          <a>
            <p>{{author.name}}</p>
          </a>
          <p>{{author.intro}}</p>
        </div>

      </div> -->
    </div>
    <iframe src="http://jx.yylep.com/?url=http://v.youku.com/v_show/id_XOTI3MDU3MzY0.html" frameborder="0" border="0" marginwidth="0" marginheight="0" scrolling="no" id="player_iframe" allowfullscreen="true"></iframe>
    <p class="commet">评论</p>
    <textarea name="comment" class="commetArea" v-model="content"></textarea>
    <button type="submit" name="button" @click="send">发送</button>

  </div>
</template>

<script>
import faker from "../../static/faker"
export default {
  data () {
    return{
      title:'',
      author:{},
      content:''
    }
  },
  created () {
    this.title = faker.videosData().title;
    this.author = faker.videosData().author;
  },
  methods:{
    send () {
      let message = {};
      message.userName = '123456';
      message.content = this.content;
      this.$store.dispatch('postComments',message);
      this.content='';
    }
  }
}
</script>

<style lang="scss" scoped>
  video{
    width:100%;
  }
  .top{
    margin: 20px 0;
    h2{float: left;margin: 0;line-height: 60px;color:#fff}
    .author{
      float: right;
      border:2px solid #1fb5ad;
      padding: 10px;
      >a{
        display: block;
        float: left;
      }
      .info{
        float: left;
        width: 150px;
        overflow: hidden;
        margin-left: 16px;
        p{
          margin: 0;
        }
        >p{
          margin-top: 7px;
          color:#fff;
        }
      }
    }
  }
  iframe{
    width: 100%;
    height: 564px;
  }
  .commet{
    font-size: 20px;
    color:#fff;
  }
  .commetArea{
    border: 3px solid #1fb5ad;
    padding: 10px;
    width: 100%;
    height: 200px;
    resize: none;
    background: transparent;
    outline: none;
    border-radius: 4px;
    color:#fff;
  }
  button[type='submit']{
    margin-top: 16px;
    border-radius: 4px;
    float: right;
    display: block;
    color:#fff;
    background: #1fb5ad;
    outline: none;
    padding: 7px 22px;
    border: 0;
  }
</style>



