<template lang="html">
  <div class="sp">
    <router-link :to="{ name: 'Articles', params: {id:list.id} }" class="list clearFix" v-for="list in lists" :key="list.id">
        <h3 class="title">
          {{ list.title }}
        </h3>
        <div class="category">分类: {{ list.category.name }}</div>
        <div class="brief">
          {{ list.summary }}
        </div>

    </router-link>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default  {
  computed:mapGetters({
    lists: 'comSpItem'
  })
}
</script>

<style lang="scss" scoped>
  .sp{
    .list{
      background: #1fb5ad;
      display: block;
      padding: 16px;
      margin-bottom: 20px;

      .category{
        color:#fafafa;
        font-size: 14px;
      }

      >div{
        margin-bottom: 16px;
      }
    }

  }
</style>



