import Vue from 'vue'
import Vuex from 'vuex'
import spColum from './modules/spColum'
import live from './modules/live'
import comment from './modules/comment'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    spColum,live,comment
  },

})



